/**********BEGIN LIBRARY**********/

.bookRadio {
    display: none;
}

#mainLibrary h1,
#mainLibrary h3 {
    text-align: center;
}

#coversContainer {
    text-align: center;
}

#mainLibrary .cover {
    display: inline;
}

#mainLibrary .books img {
    margin-left: 1em;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-right: 1em;
    display: inline;
    border: 1px solid black;
}

#mainLibrary .cover img {
    margin-left: 1em;
    margin-top: 1em;
    margin-bottom: 1em;
    margin-right: 1em;
    height: 18em;
}

#mainLibrary .books {
    text-align: center;
}


/*WIDE SCREEN*/

@media only screen and (min-width: 1001px) {
    #mainLibrary .books img {
        height: 18em;
    }
}


/*NARROW SCREEN*/

@media only screen and (max-width: 1000px) {
    #mainLibrary .books img {
        height: 14em;
    }
}

#mainLibrary .books {
    overflow-y: auto;
    margin-bottom: 1em;
}

#mainLibrary .books img:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.cover {
    width: 40%;
}

.overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 500ms;
    visibility: hidden;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.bookLabel {
    cursor: pointer;
}

.popup {
    background: #fff;
    border-radius: 5px;
    width: 80%;
    position: relative;
    transition: all 1s ease-in-out;
    max-height: 100vh;
}

.popup h2 {
    margin-top: 0;
    padding-top: .5em;
    text-align: center;
}

.popup .close {
    position: absolute;
    transition: all 200ms;
    font-size: 2em;
    font-weight: bold;
    text-decoration: none;
    color: $fontColor;
}

.popup .close:hover {
    color: $hoverColor;
}

.popup .books {
    max-height: 80vh;
}

//wide
@media only screen and (min-width: 1001px) {
    .popup .close {
        top: 20px;
        right: 30px;
    }
}

//narrow
@media screen and (max-width: 1000px) {
    .cover {
        width: 80%;
    }
    .popup {
        width: 80%; //make 80
    }
    .popup .close {
        top: 40px;
        right: 30px;
    }
    .noNAV:checked~.fixed-mobile-nav,
    .noNAV:checked~#menu,
    .noNAV:checked~nav {
        visibility: hidden;
        opacity: 0;
        transition: .25s ease-in-out;
    }
    /*can't click if nav is open*/
    .nav-trigger:checked~#wrapper>#content>#mainLibrary>#coversContainer>.cover>.bookLabel {
        pointer-events: none;
    }
    .noNAV:checked~#nav-trigger {
        display: none;
    }
}


/**********END LIBRARY**********/
